import axios from 'axios';

const API_BASE_URL = 'http://localhost:8000';

// 文献API服务
export class LiteratureAPI {
  // 获取认证头
  private static getAuthHeaders() {
    const token = localStorage.getItem('token');
    return {
      'Authorization': `Bearer ${token}`,
    };
  }

  // 上传私人文献
  static async uploadPrivateLiterature(file: File, title?: string) {
    const formData = new FormData();
    formData.append('file', file);
    if (title) {
      formData.append('title', title);
    }

    const response = await axios.post(
      `${API_BASE_URL}/literature/upload/private`,
      formData,
      {
        headers: {
          ...this.getAuthHeaders(),
          'Content-Type': 'multipart/form-data',
        },
      }
    );
    return response.data;
  }

  // 上传课题组文献
  static async uploadGroupLiterature(file: File, groupId: string, title?: string) {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('group_id', groupId);
    if (title) {
      formData.append('title', title);
    }

    const response = await axios.post(
      `${API_BASE_URL}/literature/upload`,
      formData,
      {
        headers: {
          ...this.getAuthHeaders(),
          'Content-Type': 'multipart/form-data',
        },
      }
    );
    return response.data;
  }

  // 获取私人文献列表
  static async getPrivateLiterature() {
    const response = await axios.get(
      `${API_BASE_URL}/literature/private`,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // 获取课题组文献列表
  static async getGroupLiterature(groupId: string) {
    const response = await axios.get(
      `${API_BASE_URL}/literature/public/${groupId}`,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // 获取用户课题组列表
  static async getUserGroups() {
    const response = await axios.get(
      `${API_BASE_URL}/user/groups`,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // 查看文献文件（预览）
  static async viewLiterature(literatureId: string) {
    const response = await axios.get(
      `${API_BASE_URL}/literature/view/file/${literatureId}`,
      {
        headers: this.getAuthHeaders(),
        responseType: 'blob',
      }
    );
    return response;
  }

  // 下载文献文件
  static async downloadLiterature(literatureId: string) {
    const response = await axios.get(
      `${API_BASE_URL}/literature/download/${literatureId}`,
      {
        headers: this.getAuthHeaders(),
        responseType: 'blob',
      }
    );
    return response;
  }

  // 删除文献
  static async deleteLiterature(literatureId: string, reason?: string) {
    const response = await axios.delete(
      `${API_BASE_URL}/literature/${literatureId}`,
      {
        headers: this.getAuthHeaders(),
        data: reason ? { reason } : {},
      }
    );
    return response.data;
  }

  // 获取文献详情
  static async getLiteratureDetail(literatureId: string) {
    const response = await axios.get(
      `${API_BASE_URL}/literature/detail/${literatureId}`,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // 退出课题组
  static async leaveGroup(groupId: string) {
    const response = await axios.post(
      `${API_BASE_URL}/groups/${groupId}/leave`,
      {},
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // 获取课题组信息
  static async getGroupInfo(groupId: string) {
    const response = await axios.get(
      `${API_BASE_URL}/groups/${groupId}/info`,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // 获取课题组成员列表
  static async getGroupMembers(groupId: string) {
    const response = await axios.get(
      `${API_BASE_URL}/groups/${groupId}/members`,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // === AI研究助手相关API ===

  // AI问答
  static async askAI(params: {
    question: string;
    literature_id: string;
    session_id?: string;
    max_sources?: number;
    include_history?: boolean;
  }) {
    const response = await axios.post(
      `${API_BASE_URL}/ai/ask`,
      params,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // 获取预设问题
  static async getPresetQuestions(literatureId: string) {
    const response = await axios.get(
      `${API_BASE_URL}/ai/preset-questions/${literatureId}`,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // 获取用户会话列表
  static async getAISessions(params?: {
    literature_id?: string;
    limit?: number;
    offset?: number;
  }) {
    const queryParams = new URLSearchParams();
    if (params?.literature_id) queryParams.append('literature_id', params.literature_id);
    if (params?.limit) queryParams.append('limit', params.limit.toString());
    if (params?.offset) queryParams.append('offset', params.offset.toString());

    const response = await axios.get(
      `${API_BASE_URL}/ai/sessions${queryParams.toString() ? '?' + queryParams.toString() : ''}`,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // 获取对话历史
  static async getConversationHistory(sessionId: string, params?: {
    include_full_content?: boolean;
    limit?: number;
  }) {
    const queryParams = new URLSearchParams();
    if (params?.include_full_content !== undefined) {
      queryParams.append('include_full_content', params.include_full_content.toString());
    }
    if (params?.limit) queryParams.append('limit', params.limit.toString());

    const response = await axios.get(
      `${API_BASE_URL}/ai/conversation/${sessionId}${queryParams.toString() ? '?' + queryParams.toString() : ''}`,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // 删除对话会话
  static async deleteConversation(sessionId: string) {
    const response = await axios.delete(
      `${API_BASE_URL}/ai/conversation/${sessionId}`,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // 提交反馈
  static async submitFeedback(params: {
    turn_id: string;
    rating: number;
    feedback?: string;
  }) {
    const response = await axios.post(
      `${API_BASE_URL}/ai/feedback`,
      params,
      {
        headers: this.getAuthHeaders(),
      }
    );
    return response.data;
  }

  // === 缓存管理相关API ===

  // 获取缓存统计
  static async getCacheStats() {
    const response = await axios.get(`${API_BASE_URL}/admin/cache/stats`);
    return response.data;
  }

  // 清理缓存
  static async clearCache(cacheType?: string) {
    const queryParams = new URLSearchParams();
    if (cacheType) queryParams.append('cache_type', cacheType);

    const response = await axios.post(
      `${API_BASE_URL}/admin/cache/clear${queryParams.toString() ? '?' + queryParams.toString() : ''}`,
      {},
    );
    return response.data;
  }
}

// 获取文献详情
export const getLiteratureDetail = async (literatureId: string) => {
  return axios.get(`/literature/detail/${literatureId}`);
};

// 预览文献
export const previewLiterature = async (literatureId: string) => {
  return axios.get(`/literature/view/file/${literatureId}`, {
    responseType: 'blob' // 处理二进制响应
  });
};

// 下载文献
export const downloadLiterature = async (literatureId: string) => {
  return axios.get(`/literature/download/${literatureId}`, {
    responseType: 'blob'
  });
};